<template>
    <div class="row">
      <div class="col-12" v-if="ready">
        <div class="card">
        <form enctype="multipart/form-data" class="form-horizontal" @submit.prevent="submitCircular()">
          <div class="card-header">
            <strong>eCircular</strong>
          </div>
          <div class="card-body card-block">
            <!-- circular type-->
            <div class="row form-group">
              <div class="col col-md-3">
                <label class=" form-control-label">Circular Type</label>
              </div>
              <div class="col col-md-9">
                <div class="form-check-inline form-check">
                  <label for="inline-radio1" class="form-check-label ">
                    <input type="radio"  v-model="feeType" value="regular"/>Without Fee Collection
                  </label>
                  <label for="inline-radio2" class="form-check-label ">
                    <input type="radio" v-model="feeType" value="fee"/>With Fee Collection
                  </label>
                </div>
              </div>
            </div>
            <!-- activity type-->
            <div class="row form-group">
              <div class="col col-md-3">
                <label class=" form-control-label">Activity Type</label>
              </div>
              <div class="col col-md-9">
                <div class="form-check-inline form-check">
                  <label for="inline-radio1" class="form-check-label ">
                    <input type="radio" v-model="activityType" value="AAC">AAC
                  </label>
                  <label for="inline-radio2" class="form-check-label ">
                    <input type="radio" v-model="activityType" value="CCA">CCA
                  </label>
                </div>
              </div>
            </div>
            <!-- Issue Date-->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Issue Date</label>
              <input type="date" v-model="issueDate" class="form-control" style="width:95%"> 
            </div>   

            <!-- Title -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Title</label>
              <textarea class="form-control" maxlength="100" rows="2" v-model="title" required/>       
            </div>
            <!-- organizer -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Organizer</label>
              <textarea class="form-control" maxlength="100" rows="2" v-model="organizer" required/>       
            </div>
            <!-- nature -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Nature</label>
              <textarea class="form-control" maxlength="100" rows="2" v-model="nature" required/>       
            </div>
            <!-- target -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Target Students</label>
              <textarea class="form-control" maxlength="100" rows="2" v-model="targetStudents" required/>       
            </div>
            <!-- Number of students-->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Number of Students</label>
              <textarea class="form-control" maxlength="100" rows="2" v-model="noOfStudents" required/>       
            </div>
            <!-- Teacher(s)-in-charge -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Teacher(s)-in-charge</label>
              <textarea class="form-control" maxlength="100" rows="2" v-model="teacher" required/>       
            </div>
            <!-- Activity Date -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Activity Date</label>
              <input type="date" v-model="activityDate" class="form-control" style="width:95%"> 
            </div>
            <!-- Activity Time -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Activity Time</label>
              <input type="time" v-model="activityTime" class="form-control" style="width:95%"> 
            </div>
            <!-- Venue -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Venue</label>
              <textarea class="form-control" maxlength="100" rows="2" v-model="venue" required></textarea>       
            </div>
            <!-- Fee -->
            <div v-if="feeType=='fee'">
              <label style="margin-top:0.2em;margin-right:0.4em;">Fee</label>
              <textarea class="form-control" maxlength="100" rows="2" cols="50" v-model="fee"></textarea>       
            </div>
            
            <!-- Remarks -->
            <div style="display:flex;flex-direction:row;margin-top:0.4em">
              <label style="margin-top:0.2em;margin-right:0.4em;">Remarks</label>
              <textarea class="form-control" maxlength="100" rows="2" cols="50" v-model="remarks" required/>       
            </div>
            
          </div>
          <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="reset" class="btn btn-danger btn-sm">
              <i class="fa fa-ban"></i> Reset
            </button>
          </div>
        </form>
        </div>
      </div>
  </div>
</template>

<script>

import axios from 'axios';
export default {
    data(){
        return{
            ready:false,
            feeType:'regular',
            activityType:'',
            issueDate:'',
            title:'',
            organizer:'',
            nature:'',
            targetStudents:'',
            noOfStudents:'',
            teacher:'',
            activityDate:'',
            activityTime:'',
            venue:'',
            remarks:'',
            fee:'',
        }
    },   
    async created(){
      console.log(this.ready);
      this.ready=true;
    },
  methods: {
      submitCircular(){
          console.log('test');
          var formdata = new FormData();
          formdata.append("feeType",this.feeType);
          formdata.append("activityType",this.activityType);
          formdata.append("issueDate",this.issueDate);
          formdata.append("title",this.title);
          formdata.append("organizer",this.organizer);
          formdata.append("nature",this.nature);
          formdata.append("targetStudents",this.targetStudents);
          formdata.append("noOfStudents",this.noOfStudents);
          formdata.append("teacher",this.teacher);
          formdata.append("activityDate",this.activityDate);
          formdata.append("activityTime",this.activityTime);
          formdata.append("venue",this.venue);
          formdata.append("remarks",this.remarks);
          axios.post("eCircular/backend/submitForm.php",formdata)
          .then(
              (response)=>{
                  this.$alert(response);
              }
          )
      },
    click () {
      // do nothing
    }
  }
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
</style>


